import { KeyModel } from "./index";
import { JsonProperty } from '../common/map-utils';

//  JsonProperty    require()
import sysuser = require("./sysuser.model");
import SysUserModel = sysuser.SysUserModel;

/**
 * . 
 */
export class ContactModel extends KeyModel {
    
    active: boolean;

    address: string;

    kind: string;

    name: string; 

    constructor() {
        super();

        this.active = false;
        this.address = undefined;
        this.kind = undefined;
        this.name = undefined;
    }
}

/**
 * . 
 */
export class EmployeeModel extends KeyModel {
   
    /**
     * 
     */
    name: string;

    /**
     * id 
     */
    orgId: number;

    /**
     *  
     */
    job: string;

    /**
     * 
     */
    position: string;    

    /**
    *  
    */
    @JsonProperty({ clazz: SysUserModel })
    user: SysUserModel;

    /**
    * 
    */
    @JsonProperty({ clazz: ContactModel })
    contacts?: ContactModel[];    

    constructor() {

        super();
        this.id = undefined;
        this.name = undefined;
        this.orgId = undefined;
        this.position = undefined;

        this.user = undefined;
        this.contacts = undefined;
    }
}


